.navigation__list {
  text-align: center;

  @media (min-width: $tablet) {
    text-align: left;
    margin-left: 57px;
    margin-right: 57px;
  }

  @media (min-width: $desktop) {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-left: 0;
    margin-right: 0;

    &::after {
      position: absolute;
      content: "";
      width: 60px;
      height: 2px;
      background-color: $basic-white;
      top: 50%;
      transform: translateY(-50%);
      left: -105px;
    }
  }
}

.navigation__item {
  @media (min-width: $desktop) {
    margin-right: 33px;
  }
}

.navigation__link {
  font-weight: 500;
  text-transform: uppercase;
  color: $basic-blue;
  font-size: 30px;
  line-height: 35px;

  @media (min-width: $tablet) {
    font-size: 60px;
    line-height: 71px;
  }

  @media (min-width: $desktop) {
    font-size: 20px;
    line-height: 20px;
    color: $basic-white;
  }
}

.navigation--menu-scroll .navigation__link {
  @media (min-width: $desktop) {
    color: $basic-blue;
  }
}

.navigation--menu-scroll .navigation__list::after {
  background-color: $basic-blue;
}
